    }
    #add-to-cart {
      width: 50%;
      background-color: #ff9a00;
      color: white;
      white-space: nowrap;
      text-align: center;
      padding-left: 0;
      padding-right: 0;
    }
    #buy-now {
      width: 50%;
      background-color: #ff4747;
      color: white;
      white-space: nowrap;
      text-align: center;
      padding-left: 0;
      padding-right: 0;
    }
</style>
